#skills{
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
}

.skillsTitle{
    font-size: 3rem;
    font-weight: 600;
    padding-top: 3rem;
    margin-bottom: 1.5rem;
}

.skillsDesc{
    font-weight: 400;
    font-size: 1rem;
    max-width: 55vw;
}

.skillBars{
    margin: 1.5rem;
    width: 100vw;
    max-width: 85%;
    text-align: left;
}

.skillBar{
    display: flex;
    margin: 1rem;
    padding: 1.5rem 2rem;
    border-radius: 0.5rem;
    background-color: #495cb2;;
}

.skillBarImg{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin-right: 2rem;
}

.skillBarText>p{
    font-size: 0.9rem;
    font-weight: 200;
}

@media screen and (max-width: 720px){
    .skillBarText>p{
        font-size: 1rem;
    }
}

@media screen and (max-width: 480px){
    .skillBarText>p{
        font-size: 1.5rem;
    }

    .skillBarImg{
        height: 2.25rem;
        width: 2.25rem;
    }

    .skillBarText{
        
    }
}